<template lang="pug">
button(type="button" :class="classes" @click="onClick" :style="style") 
  svg(v-if="icon")
  p(v-if="label") {{label}}
</template>

<script>
export default {
  name: 'my-button',

  props: {
    label: {
      type: String,
      default: '',
    },
    icon: {
      type: String,
      default: '',
    },
    theme: {
      type: String,
      default: 'filled',
      validator: function (value) {
        return ['filled', 'outline', 'flat', 'plain'].indexOf(value) !== -1;
      },
    },
    size: {
      type: String,
      default: 'medium',
      validator: function (value) {
        return ['small', 'medium', 'large'].indexOf(value) !== -1;
      },
    },
  },

  computed: {
    classes() {
      return {
        'my-button': true,
        [`my-button-theme--${this.theme}`]: true,
        [`my-button-size--${this.size}`]: true,
        'my-button--icon-only': this.icon && !this.label,
        'my-button--label-only': !this.icon && this.label,
        'my-button--icon-label': this.icon && this.label,
      };
    },
    style() {
      return {
      };
    },
  },

  methods: {
    onClick() {
      this.$emit('onClick');
    },
  },
};
</script>

<style lang="sass" scoped>
$bricks-red  : #B82C30
.my-button-theme--filled
  background-color: $bricks-red

.my-button-theme--outline
  background-color: transparent
  border: 1px solid $bricks-red

.my-button-theme--plain
  background-color: transparent
  border: none

.my-button-theme--float
  background-color: $bricks-red
</style>

